//   default: false,
//   effects_UNSTABLE: [persistAtom],
// });

export const isEditModal = atom({
  key: "editModal",
  default: false,
  effects_UNSTABLE: [persistAtom],
});

export const isType = atom({
  key: "type",
  default: "",
  effects_UNSTABLE: [persistAtom],
});
